.item{
    width: 350px;
}
.item p{
    margin: 6px 0px;
}
.item-prices{
    display: flex;
    gap: 10px;
}
.item-price-new{
    color: #374151;
    font-size: 18px;
    font-weight: 600;
}
.item-price-old {
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}
.item:hover{
    transform: scale(1.05);
    transition: 0.6s;
}
.danger {
    background-color: red;
    width: 25%;
}
.success {
    background-color: green;
    width: 25%;
}
.primary {
    background-color: silver;
    width: 50%;
}
.btn {
    border: none;
    height: 30px;
    border-radius: 30px;
    color: white;
}